body {
    margin: 0;
    padding: 0;
    background-image: linear-gradient(120deg, #d4fc79 0%, #96e6a1 100%);

}

html {
    font-size: 62.5%;
}

.nav {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0.5rem;
}

.ul {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10rem;


}

.li {
    list-style: none;
    font-size: 2rem;


}

.li:hover {
    font-size: 2.2rem;
    color: red;
    text-decoration: underline;
}

.navlink {
    display: flex;

    text-decoration: none;
    justify-content: space-evenly;


}

.navlink:hover {}



.main_div {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.div {
    display: flex;
    flex-direction: column;
    gap: 2.5rem;

}



form {
    padding: 2rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 2rem;

    width: 50rem;
}

.form_heading {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 3rem;
    text-transform: uppercase;

    font-weight: bold;
}

.contact_cont {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 5rem;
    border: 2px soild black;
    border-radius: 10rem;
    margin-bottom: 10rem ;
}


.inner {

    display: flex;
    /* flex-direction: column; */
    gap: 1rem;
    /* justify-content: center; */
    align-items: center;
}

label {
    text-transform: uppercase;
    font-weight: bold;
    font-size: 1.2rem;
    margin-right: 1rem;
}

input,
textarea {
    background-color: white;
    font-size: 1.2rem;
    padding: 0.8rem;
    border: 1px solid black;
    border-radius: 10rem;
}


.submit_btn {
    background-color: black;
    color: white;
    padding: 1rem;
    margin: 1rem;
    width: 60%;
    font-size: 1.2rem;
    border: 2px soid black;
    border-radius: 7rem;
    display: flex;
    justify-content: center;
    align-items: center;
}


.footer{
    background-color: black;
    color: white;
    font-size: 1.2rem;
    position: absolute;
    text-align: center;
    bottom: 0rem;
    padding: 1rem;
    width: 100%;

}

@media (max-width : 320px) {
    html {
        font-size: 35%;
    }

}



@media (min-width : 320px) and (max-width : 480px) {
    html {
        font-size: 43.75%;
    }

}

@media (min-width : 481px) and (max-width : 768px) {
    html {
        font-size: 53.125%;
    }

}

@media (min-width : 769px) and (max-width : 1023px) {
    html {
        font-size: 62.5%;
    }

}


@media (min-width : 1024px) and (max-width : 1200px) {
    html {
        font-size: 71.85%;
    }

}


@media (min-width : 1201px) {
    html {
        font-size: 77%;
    }

}